import React from 'react';
import Button from 'react-bootstrap/Button'
class Notes extends React.Component {
    componentDidMount () {
        window.scrollTo(0,document.body.scrollHeight);
    }
    render() {
        const notes = this.props.notes; 
        let list;
        if (notes !== undefined && notes !== null) {
            list = <ul style={{width:'50%', margin:'auto'}}>
            {notes.map(note =>
                <li key={note.id}><span>{note.task}</span></li>
            )}
            </ul>;
        }
        return (
            // orientation="right" className="d-none d-lg-block"
            <div  className="text-center" >
                <Button onClick={this.addNote} variant="dark" style={{borderColor: '#64ffda',}}>Add Note</Button>
                <p>Notes List</p>
                {list}
                
            </div>
        );
    }
}

export default Notes;